<template>
  <div>
    <div id="map" class="hidden-xs-and-up"></div>
    <v-container id="pharmaciesList">
      <pull-to :top-load-method="searchPosition" :top-config="pull">
        <v-container id="pharmaciesList">
          <v-layout column>
            <v-expansion-panel popout expand>
              <v-expansion-panel-content
                v-for="pharma in pharmas"
                :key="pharma.name"
                expand-icon="local_pharmacy"
              >
                <template v-slot:header>
                  <div>{{pharma.name}}</div>
                </template>
                <pharma-infos
                  :devlat="lat"
                  :devlon="lon"
                  :pharmaid="'pharma'+pharmas.indexOf(pharma)"
                  :nom="pharma.name"
                  gerant="Jean de la Molfesse"
                  :lat="pharma.geometry.location.lat"
                  :lon="pharma.geometry.location.lng"
                  :adresse="pharma.vicinity"
                ></pharma-infos>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-layout>
        </v-container>
      </pull-to>
    </v-container>
    <v-btn id="refresh" v-on:click="searchPosition" fab dark class="indigo hidden-sm-and-down">
      <v-icon dark>refresh</v-icon>
    </v-btn>
  </div>
</template>

<script>
import PharmaInfos from "./PharmacieInfos";
import PullTo from "vue-pull-to";

export default {
  data: function() {
    return {
      lat: 0,
      lon: 0,
      pharmas: [],
      pull: {
        pullText: "Tirez un peu plus", // The text is displayed when you pull down
        triggerText: "Relâchez pour rafraichir", // The text that appears when the trigger distance is pulled down
        loadingText: "Chargement...", // The text in the load
        doneText: "Terminé !", // Load the finished text
        failText: "Erreur :(", // Load failed text
        loadedStayTime: 100, // Time to stay after loading ms
        stayDistance: 50, // Trigger the distance after the refresh
        triggerDistance: 70 // Pull down the trigger to trigger the distance
      }
    };
  },
  components: {
    PharmaInfos,
    PullTo
  },
  computed: {
    myPosition: function() {
      return new google.maps.LatLng(this.lat, this.lon);
    },
    map: function() {
      return new google.maps.Map(document.getElementById("map"), {
        center: this.myPosition,
        zoom: 15
      });
    },
    service: function() {
      return new google.maps.places.PlacesService(this.map);
    }
  },
  methods: {
    geolocationSuccess: function(position) {
      this.lat = position.coords.latitude;
      this.lon = position.coords.longitude;
      console.log(this.lat);
      this.pharmas = this.searchPharmas();
    },
    geolocationError: function(error) {
      console.log(error);
      if (error.code == 3) {
        alert(
          "Veuillez activer la géolocalisation géolocalisatrice de la géolocalité svp :)"
        );
      } else {
        alert(`code: ${error.code}\nmessage: ${error.message}\n`);
      }
    },
    searchPosition: function(loaded) {
      navigator.geolocation.getCurrentPosition(this.geolocationSuccess, this.geolocationError,{timeout:3000});
      loaded('done');
    },
    searchPharmas: function() {
      const res = [];
      console.log("hey");
      const req = new XMLHttpRequest();
      req.open('GET','https://cors-anywhere.herokuapp.com/https://maps.googleapis.com/maps/api/place/nearbysearch/json?location='+this.lat+','+this.lon+'&rankby=distance&type=pharmacy&key=AIzaSyDxWZwNUysm95GtigT_oznJgeayse8wq2s',false);
      req.send(null);
      if (req.status ===200){
        //console.log(req.response);
        const rep = JSON.parse(req.response);
        console.log(rep.results);
        for (let i = 0; i < rep.results.length; i++) {
          res.push(rep.results[i]);
        }
      }/*
      this.service.nearbySearch(
      {
        location: this.myPosition,
        type: "pharmacy",
        rankBy:google.maps.places.RankBy.DISTANCE
      }, function(results, status) {
        if (status !== "OK") {
          alert(status);
        }
        for (let i = 0; i < results.length; i++) {
          res.push(results[i]);
        }
      });*/
      console.log(res);
      return res;
    }
  }
};
</script>

<style>
#refresh {
  position: fixed;
  padding: 10px;
  bottom: 15px;
  right: 15px;
}
@media (max-width: 600px) {
  #refresh {
    position: fixed;
    padding: 10px;
    top: 15px;
    bottom: auto;
    right: 15px;
  }
}
</style>



